import { useEffect, useState } from 'react'
import { useCheckIsUserQuery } from '../../api/auth/auth.query'
import { CartProductList } from '../../api/cart/cart.interface'
import { useGetCartListQuery } from '../../api/cart/cart.query'
import { BASE_URL } from '../../api/constant'
import AppHeader from '../../core/components/AppHeader'
import * as UI from './Order.styled'

export default function Order() {
  const { data, isLoading, isError } = useGetCartListQuery()
  const { data: userData } = useCheckIsUserQuery()
  const [result, setResult] = useState(0)

  useEffect(() => {
    if (!data) return
    const a = data?.content.map((item: any) => item.product.price * item.amount)
    const result = a.reduce((previousValue: any, currentValue: any) => previousValue + currentValue, 0)
    setResult(result)
  }, [data])

  return (
    <>
      <AppHeader title="결제" isBack />
      <UI.Wrap>
        <UI.OrderInfo>
          <strong>총 {data?.content.length}개의 상품</strong>
          {data?.content.map((item: any) => (
            <ul>
              <li>
                <UI.BrandTitle>
                  {/* <img src={'https://gram-img.s3.ap-northeast-2.amazonaws.com/upload-img/default_user.jpeg'} /> */}
                  <strong>{item.product.brand_name}</strong>
                </UI.BrandTitle>
                <UI.Hr />
                <UI.BrandItem>
                  <img src={BASE_URL + '/' + item.product.profile_image_url} alt={item.product.name} />
                  <div>
                    <strong>{item.product.name}</strong>
                    <span>{item.amount}개</span>
                  </div>
                  <strong>{(item.product.price * item.amount).toLocaleString()}원</strong>
                </UI.BrandItem>
              </li>
            </ul>
          ))}
        </UI.OrderInfo>
        <UI.PayInfo>
          <UI.UserInfo>
            <strong>{userData?.name}</strong>
            <p>{userData?.phone}</p>
            <p>{userData?.address}</p>
          </UI.UserInfo>
          <div>
            <strong>총 결제 금액</strong>
            <span>{result.toLocaleString()}원</span>
          </div>
        </UI.PayInfo>
      </UI.Wrap>
    </>
  )
}
